const handleClick = (text: string, output = '0') => {
  switch (text) {
    case '0':
    case '1':
    case '2':
    case '3':
    case '4':
    case '5':
    case '6':
    case '7':
    case '8':
    case '9':
      if (output === '0') {
        return text;
      } else {
        return output += text;
      }
    case '.':
      if (output.includes('.')) {
        return output;
      }
      return output += text;
    case '删除':
      if (output.length === 1) {
        return '0';
      } else {
        return output.slice(0, -1);
      }
    case '清空':
      return '0';
    default:
      return '';
  }

};
const generateOutput = (text: string, output = '0') => {
  output = handleClick(text, output);
  if (output.length > 16) {
    output = output.slice(0, 16);
  } else if (output.length === 0) {
    output = '0';
  }
  return output;
};
export {generateOutput};